import { Component, OnInit } from '@angular/core';
import {ListenReads} from './listen-read';

@Component({
  selector: 'app-listen-read',
  templateUrl: './listen-read.component.html',
  styleUrls: ['./listen-read.component.scss']
})
export class ListenReadComponent implements OnInit {

  lessons: any[];
  fakeLessons: any[];
  selectedLesson: string = '0';
  constructor() { }

  ngOnInit(): void {
    console.log('selectedLesson1', this.selectedLesson);
    this.getLessons();
    this.defaultLesson();
  }

  getLessons() {
    this.lessons = ListenReads;
  }

  collapsed(event: any): void {
    // console.log(event);
  }

  expanded(event: any): void {
    // console.log(event);
  }

  onChangeLesson(e) {
    this.selectedLesson = e.target.value;
    if (this.selectedLesson === '0') {
      this.defaultLesson();
    } else {
      this.fakeLessons = this.lessons.filter(x => x.id === parseInt(this.selectedLesson, 10));
    }
  }

  defaultLesson() {
    this.fakeLessons = this.lessons;
  }
}
