import { Component, OnInit, ViewChild } from '@angular/core';
import confetti from 'canvas-confetti';

@Component({
  selector: 'app-happy-birthday-bx',
  templateUrl: './happy-birthday-bx.component.html',
  styleUrls: ['./happy-birthday-bx.component.scss']
})
export class HappyBirthdayBxComponent implements OnInit {
  @ViewChild('largeModal') largeModal;

  constructor() { }

  ngOnInit(): void {
  }

  showVideo() {
    this.largeModal.show();
    const duration = 15 * 1000;
    const animationEnd = Date.now() + duration;
    let skew = 1;
    const end = Date.now() + (15 * 1000);

// go Buckeyes!
    const colors = ['#90EE90', '#ffffff'];

    (function frame() {
      const timeLeft = animationEnd - Date.now();
      const ticks = Math.max(200, 500 * (timeLeft / duration));
      skew = Math.max(0.8, skew - 0.001);

      confetti({
        particleCount: 2,
        angle: 60,
        spread: 1000,
        origin: { x: 0 },
        colors: colors
      });
      confetti({
        particleCount: 2,
        angle: 120,
        spread: 1000,
        origin: { x: 1 },
        colors: colors
      });

      if (Date.now() < end) {
        requestAnimationFrame(frame);
      }
    }());
  }

}
