import { Component, OnInit } from '@angular/core';
import {Grammars} from './grammar';

@Component({
  selector: 'app-grammar',
  templateUrl: './grammar.component.html',
  styleUrls: ['./grammar.component.scss']
})
export class GrammarComponent implements OnInit {

  lessons: any[];
  selectedLesson = '0';
  fakeLessons: any[];

  constructor() { }

  ngOnInit(): void {
    this.getLessons();
    this.defaultLesson();
  }

  collapsed(event: any): void {
    // console.log(event);
  }

  expanded(event: any): void {
    // console.log(event);
  }

  // Try to mute all video and audio elements on the page
  stopAll() {
    document.querySelectorAll(`video, audio`).forEach(( elem: any) => {
      // Mute a singular HTML5 element
      elem.load();
    });
  }

  onChangeLesson(e) {
    this.selectedLesson = e.target.value;
    if (this.selectedLesson === '0') {
      this.defaultLesson();
    } else {
      this.fakeLessons = this.lessons.filter(x => x.id === parseInt(this.selectedLesson, 10));
    }
  }

  private getLessons() {
    this.lessons = Grammars;
  }

  private defaultLesson() {
    this.fakeLessons = this.lessons;
  }
}
